<script setup>
import { loginRequest } from "@/requests/authRequest";
import { ref } from "vue";
import useUser from "@/store/user";
import { useRouter } from "vue-router";

// store define
const userStore = useUser();
const router = useRouter();

// component data
const userData = ref({
  phone: null,
  password: null,
});

const validationErrors = ref({
  phone: null,
  password: null,
});

// component methods
const loginSubmit = async () => {
  try {
    await loginRequest(userData.value);
    userStore.fetchUser();
    router.replace({name:'home'});
  } catch (error) {
    if (error.response.status == 422) {
      validationErrors.value = error.response.data.errors;
    } else {
      console.log(error.response);
    }
  }
};
</script>

<template>
  <div class="flex flex-row justify-center">
    <div
      class="bg-white bg-opacity-70 rounded-md flex flex-col items-center max-w-lg p-4 mb-12"
    >
      <img src="../../assets/logo.png" width="400" alt="logo" />
      <div class="w-full mb-4">
        <div class="form-control bg-white">
          <input
            v-model="userData.phone"
            id="phone"
            type="text"
            class="input-control"
            placeholder=" "
          />
          <label for="phone" class="label-control">شماره همراه</label>
        </div>
        <span v-if="validationErrors.phone" class="invalid">{{
          validationErrors.phone[0]
        }}</span>
      </div>
      <div class="w-full mb-4">
        <div class="form-control bg-white">
          <input
            v-model="userData.password"
            id="password"
            type="password"
            class="input-control"
            placeholder=" "
          />
          <label for="password" class="label-control">رمز ورود</label>
        </div>
        <span v-if="validationErrors.password" class="invalid">{{
          validationErrors.password[0]
        }}</span>
      </div>
      <button
        @click="loginSubmit"
        class="w-full bg-palette1-green-1 rounded-md py-2 cursor-pointer text-white"
      >
        ورود
      </button>
    </div>
  </div>
</template>
